body {
  background-color: #1b263b;
}
.banner {
  background-color: #1b263b;
  font-size: 35px;
  color: #e0e1dd;
  font-family: 'Big Shoulders Stencil Text', cursive;
  display: flex;
  justify-content: space-between;
  padding: 0.7em;
  position: sticky;
  top: 0;
}
.banner a {
  color: #e0e1dd;
  text-decoration: none;
}
.container {
  width: 60em;
  margin: 0 auto;
  padding: 1em;
}
.movies {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  background-color: #1b263b;
}
.movie-card {
  height: auto;
  width: 200px;
  margin: 1em 0;
}
.movie-card a {
  text-decoration: none;
  color: #e0e1dd;
}
h3.movie_title {
  margin: 2px 2px 5px 2px;
  font-size: 1.2em;
  text-align: center;
  font-family: 'Noto Sans KR', sans-serif;
  font-weight: 500;
}
.movie_genres {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.genres_genre {
  border: 2px solid #e0e1dd;
  padding: 4px;
  border-radius: 0.8em;
  margin: 2px 4px;
  color: #e0e1dd;
  font-size: 0.9em;
  font-family: 'Noto Sans KR', sans-serif;
}
h1.loader_text {
  color: #e0e1dd;
  font-size: 40px;
  letter-spacing: 5px;
  font-family: 'Big Shoulders Stencil Text', cursive;
}
.loader {
  margin: 0 auto;
}
.detail-movie {
  display: flex;
}
.detail-img img {
  height: 500px;
  width: 400px;
  margin-right: 30px;
}
.detail-text {
  color: #e0e1dd;
}
.big-text {
  font-size: 18px;
}
